"use client";

import * as React from "react";
import { memo, useMemo, useCallback, useRef, DependencyList } from "react";

export interface MemoizationConfig {
  maxCacheSize?: number;
  ttl?: number; // Time to live in milliseconds
  keyGenerator?: (...args: any[]) => string;
  onCacheHit?: (key: string) => void;
  onCacheMiss?: (key: string) => void;
  onCacheEvict?: (key: string, reason: "size" | "ttl") => void;
}

export interface CacheEntry<T> {
  value: T;
  timestamp: number;
  accessCount: number;
  lastAccessed: number;
}

export interface CacheStats {
  hits: number;
  misses: number;
  evictions: number;
  size: number;
  hitRate: number;
}

// Advanced LRU cache with TTL support
export class AdvancedCache<T> {
  private cache = new Map<string, CacheEntry<T>>();
  private accessOrder: string[] = [];
  private stats: CacheStats = {
    hits: 0,
    misses: 0,
    evictions: 0,
    size: 0,
    hitRate: 0,
  };

  constructor(
    private maxSize: number = 100,
    private ttl: number = 5 * 60 * 1000, // 5 minutes default
    private config: MemoizationConfig = {},
  ) {}

  get(key: string): T | undefined {
    const entry = this.cache.get(key);

    if (!entry) {
      this.stats.misses++;
      this.config.onCacheMiss?.(key);
      this.updateHitRate();
      return undefined;
    }

    // Check TTL
    if (Date.now() - entry.timestamp > this.ttl) {
      this.delete(key, "ttl");
      this.stats.misses++;
      this.config.onCacheMiss?.(key);
      this.updateHitRate();
      return undefined;
    }

    // Update access tracking
    entry.accessCount++;
    entry.lastAccessed = Date.now();

    // Move to end (most recently used)
    const index = this.accessOrder.indexOf(key);
    if (index > -1) {
      this.accessOrder.splice(index, 1);
    }
    this.accessOrder.push(key);

    this.stats.hits++;
    this.config.onCacheHit?.(key);
    this.updateHitRate();

    return entry.value;
  }

  set(key: string, value: T): void {
    const existing = this.cache.get(key);

    if (existing) {
      existing.value = value;
      existing.timestamp = Date.now();
      existing.lastAccessed = Date.now();
      return;
    }

    // Check if we need to evict
    if (this.cache.size >= this.maxSize) {
      this.evictLeastRecentlyUsed();
    }

    const entry: CacheEntry<T> = {
      value,
      timestamp: Date.now(),
      accessCount: 1,
      lastAccessed: Date.now(),
    };

    this.cache.set(key, entry);
    this.accessOrder.push(key);
    this.stats.size = this.cache.size;
  }

  private delete(key: string, reason: "size" | "ttl"): boolean {
    const deleted = this.cache.delete(key);
    if (deleted) {
      const index = this.accessOrder.indexOf(key);
      if (index > -1) {
        this.accessOrder.splice(index, 1);
      }
      this.stats.evictions++;
      this.stats.size = this.cache.size;
      this.config.onCacheEvict?.(key, reason);
    }
    return deleted;
  }

  private evictLeastRecentlyUsed(): void {
    if (this.accessOrder.length > 0) {
      const lruKey = this.accessOrder[0];
      this.delete(lruKey, "size");
    }
  }

  private updateHitRate(): void {
    const total = this.stats.hits + this.stats.misses;
    this.stats.hitRate = total > 0 ? (this.stats.hits / total) * 100 : 0;
  }

  clear(): void {
    this.cache.clear();
    this.accessOrder = [];
    this.stats = { hits: 0, misses: 0, evictions: 0, size: 0, hitRate: 0 };
  }

  getStats(): CacheStats {
    return { ...this.stats };
  }

  // Clean expired entries
  cleanup(): number {
    const now = Date.now();
    let cleaned = 0;

    const entries = Array.from(this.cache.entries());
    for (const [key, entry] of entries) {
      if (now - entry.timestamp > this.ttl) {
        this.delete(key, "ttl");
        cleaned++;
      }
    }

    return cleaned;
  }
}

// Global cache instances
const componentCache = new AdvancedCache<any>(200, 10 * 60 * 1000); // 10 minutes
const computationCache = new AdvancedCache<any>(500, 5 * 60 * 1000); // 5 minutes

// Enhanced useMemo with caching
export function useAdvancedMemo<T>(
  factory: () => T,
  deps: DependencyList,
  cacheKey?: string,
): T {
  const key = cacheKey || `memo_${JSON.stringify(deps)}`;

  // Use a more stable dependency approach - serialize deps to ensure consistency
  const depsString = JSON.stringify(deps);

  return useMemo(() => {
    // Try to get from cache first
    const cached = computationCache.get(key);
    if (cached !== undefined) {
      return cached;
    }

    // Compute new value
    const result = factory();

    // Cache the result
    computationCache.set(key, result);

    return result;
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [factory, key, depsString]);
}

// Enhanced useCallback with caching
export function useAdvancedCallback<T extends (...args: any[]) => any>(
  callback: T,
  deps: DependencyList,
  cacheKey?: string,
): T {
  const key = cacheKey || `callback_${JSON.stringify(deps)}`;

  // Use a more stable dependency approach - serialize deps to ensure consistency
  const depsString = JSON.stringify(deps);

  return useCallback(
    (...args: any[]) => {
      const argKey = `${key}_${JSON.stringify(args)}`;

      // Try to get from cache
      const cached = computationCache.get(argKey);
      if (cached !== undefined) {
        return cached;
      }

      // Execute callback
      const result = callback(...args);

      // Cache result if it's serializable
      try {
        JSON.stringify(result);
        computationCache.set(argKey, result);
      } catch {
        // Don't cache non-serializable results
      }

      return result;
      // eslint-disable-next-line react-hooks/exhaustive-deps
    },
    [callback, key, depsString],
  ) as T;
}

// Smart component memoization
export function smartMemo<P extends Record<string, any>>(
  Component: React.ComponentType<P>,
  config: MemoizationConfig = {},
): React.MemoExoticComponent<React.ComponentType<P>> {
  const {
    keyGenerator = (props: P) => JSON.stringify(props),
    maxCacheSize = 50,
    ttl = 5 * 60 * 1000,
  } = config;

  const cache = new AdvancedCache<React.ReactElement>(
    maxCacheSize,
    ttl,
    config,
  );

  const MemoizedComponent = memo(
    (props: P) => {
      const key = keyGenerator(props);

      // Try cache first
      const cached = cache.get(key);
      if (cached) {
        return cached;
      }

      // Render new
      const element = React.createElement(Component, props);
      cache.set(key, element);

      return element;
    },
    (prevProps, nextProps) => {
      // Custom comparison based on generated keys
      const prevKey = keyGenerator(prevProps);
      const nextKey = keyGenerator(nextProps);
      return prevKey === nextKey;
    },
  );

  MemoizedComponent.displayName = `SmartMemo(${Component.displayName || Component.name || "Component"})`;

  return MemoizedComponent;
}

// Batched state updates utility
export function useBatchedUpdates<T>(
  initialState: T,
  batchDelay: number = 16, // One frame
): [T, (updater: T | ((prev: T) => T)) => void, () => void] {
  const [state, setState] = React.useState(initialState);
  const batchRef = useRef<{
    updates: Array<T | ((prev: T) => T)>;
    timeoutId?: NodeJS.Timeout;
  }>({ updates: [] });

  const flushUpdates = useCallback(() => {
    if (batchRef.current.updates.length === 0) return;

    const updates = [...batchRef.current.updates];
    batchRef.current.updates = [];

    setState((prevState) => {
      let currentState = prevState;
      for (const update of updates) {
        if (typeof update === "function") {
          currentState = (update as (prev: T) => T)(currentState);
        } else {
          currentState = update;
        }
      }
      return currentState;
    });

    if (batchRef.current.timeoutId) {
      clearTimeout(batchRef.current.timeoutId);
      batchRef.current.timeoutId = undefined;
    }
  }, []);

  const batchedSetState = useCallback(
    (updater: T | ((prev: T) => T)) => {
      batchRef.current.updates.push(updater);

      if (batchRef.current.timeoutId) {
        clearTimeout(batchRef.current.timeoutId);
      }

      batchRef.current.timeoutId = setTimeout(flushUpdates, batchDelay);
    },
    [flushUpdates, batchDelay],
  );

  const forceFlush = useCallback(() => {
    if (batchRef.current.timeoutId) {
      clearTimeout(batchRef.current.timeoutId);
      batchRef.current.timeoutId = undefined;
    }
    flushUpdates();
  }, [flushUpdates]);

  // Cleanup on unmount
  React.useEffect(() => {
    const batchRefCurrent = batchRef.current;
    return () => {
      if (batchRefCurrent?.timeoutId) {
        clearTimeout(batchRefCurrent.timeoutId);
      }
    };
  }, []);

  return [state, batchedSetState, forceFlush];
}

// Performance-optimized list renderer
export function useVirtualizedList<T>(
  items: T[],
  renderItem: (item: T, index: number) => React.ReactNode,
  config: {
    itemHeight: number;
    containerHeight: number;
    overscan?: number;
    cacheSize?: number;
  },
) {
  const { itemHeight, containerHeight, overscan = 5, cacheSize = 100 } = config;

  const [scrollTop, setScrollTop] = useState(0);
  const cache = useRef(new AdvancedCache<React.ReactNode>(cacheSize));

  const visibleRange = useMemo(() => {
    const start = Math.floor(scrollTop / itemHeight);
    const end = Math.min(
      items.length - 1,
      Math.ceil((scrollTop + containerHeight) / itemHeight),
    );

    return {
      start: Math.max(0, start - overscan),
      end: Math.min(items.length - 1, end + overscan),
    };
  }, [scrollTop, itemHeight, containerHeight, items.length, overscan]);

  const visibleItems = useMemo(() => {
    const rendered: Array<{ item: T; index: number; node: React.ReactNode }> =
      [];

    for (let i = visibleRange.start; i <= visibleRange.end; i++) {
      const item = items[i];
      const cacheKey = `item_${i}_${JSON.stringify(item)}`;

      let node = cache.current.get(cacheKey);
      if (!node) {
        node = renderItem(item, i);
        cache.current.set(cacheKey, node);
      }

      rendered.push({ item, index: i, node });
    }

    return rendered;
  }, [items, visibleRange, renderItem]);

  const handleScroll = useCallback((event: React.UIEvent<HTMLDivElement>) => {
    setScrollTop(event.currentTarget.scrollTop);
  }, []);

  return {
    visibleItems,
    totalHeight: items.length * itemHeight,
    offsetY: visibleRange.start * itemHeight,
    handleScroll,
    cacheStats: cache.current.getStats(),
  };
}

// Cache management utilities
export const cacheManager = {
  getComponentCacheStats: () => componentCache.getStats(),
  getComputationCacheStats: () => computationCache.getStats(),
  clearComponentCache: () => componentCache.clear(),
  clearComputationCache: () => computationCache.clear(),
  cleanupExpired: () => {
    const componentCleaned = componentCache.cleanup();
    const computationCleaned = computationCache.cleanup();
    return { componentCleaned, computationCleaned };
  },
  getTotalCacheStats: () => {
    const compStats = componentCache.getStats();
    const compuStats = computationCache.getStats();

    return {
      totalHits: compStats.hits + compuStats.hits,
      totalMisses: compStats.misses + compuStats.misses,
      totalEvictions: compStats.evictions + compuStats.evictions,
      totalSize: compStats.size + compuStats.size,
      averageHitRate: (compStats.hitRate + compuStats.hitRate) / 2,
    };
  },
};

// Performance debugging utilities
export const performanceDebugger = {
  logCacheStats: () => {
    console.group("üöÄ Performance Cache Stats");
    console.table(cacheManager.getTotalCacheStats());
    console.groupEnd();
  },

  measureRenderTime: <T extends (...args: any[]) => any>(
    fn: T,
    label: string = "Render",
  ): T => {
    return ((...args: any[]) => {
      const start = performance.now();
      const result = fn(...args);
      const end = performance.now();
      console.log(`‚è±Ô∏è ${label}: ${(end - start).toFixed(2)}ms`);
      return result;
    }) as T;
  },

  profileComponent: <P extends Record<string, any>>(
    Component: React.ComponentType<P>,
    componentName: string,
  ) => {
    const ProfiledComponent = memo((props: P) => {
      const renderStart = performance.now();

      React.useEffect(() => {
        const renderEnd = performance.now();
        console.log(
          `üîç ${componentName} rendered in ${(renderEnd - renderStart).toFixed(2)}ms`,
        );
      });

      return React.createElement(Component, props);
    });

    ProfiledComponent.displayName = `Profiled(${componentName})`;

    return ProfiledComponent;
  },
};

import { useState, useEffect } from "react";
